export const wttrInRoute = (lat: string, lon: string) =>
  `https://wttr.in/${lat},${lon}?format=j1`;

export const altWttrInRoute = (location: string) =>
  `https://wttr.in/${location}?format=j1`;

const apiKey = process.env.REACT_APP_OPENWEATHERMAP_API_KEY;

export const openWeatherMapRoute = (lat: string, lon: string) =>
  `https://api.openweathermap.org/data/2.5/weather?lat=${lat}&lon=${lon}&appid=${apiKey}`;

export const altOpenWeatherMapRoute = (lat: string, lon: string, limit = 5) =>
  `https://api.openweathermap.org/geo/1.0/reverse?lat=${lat}&lon=${lon}&limit=${limit}&appid=${apiKey}`;
